import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./contract-type-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class ContractTypeAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
//	@ViewChild(ToastComponent) toast: ToastComponent;

 
contractTypeForm: FormGroup;
contracttypeId = 0;  
ajaxData : ContractTypeData[];
contracttype= new ContractTypeData();
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.contracttypeId).subscribe(params=>{
			if(params.contracttypeId != undefined){
				this.contracttypeId=params.contracttypeId; 
			}
		}); 
	console.log("contracttypeId=="+this.contracttypeId);
	if(this.contracttypeId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.contractTypeForm = new FormGroup({
			'contracttypeId' : new FormControl(''),
			'contracttypeName' : new FormControl('', [ Validators.required]),
			'contracttypeEntryId' : new FormControl(''),
			'contracttypeEntryDate' : new FormControl(''),
			'contracttypeModifiedId' : new FormControl(''),
			'contracttypeModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get contracttypeName(){ return this.contractTypeForm.get('contracttypeName')};
	    get contracttypeEntryId(){ return this.contractTypeForm.get('contracttypeEntryId')};
	    get contracttypeEntryDate(){ return this.contractTypeForm.get('contracttypeEntryDate')};
	    get contracttypeModifiedId(){ return this.contractTypeForm.get('contracttypeModifiedId')};
	    get contracttypeModifiedDate(){ return this.contractTypeForm.get('contracttypeModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<ContractTypeData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/contracttype-data ',
							 			JSON.stringify({ 'contracttypeId' :this.contracttypeId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.contracttype = data['populateData'][0];
																		console.log("contracttypeModifiedId=="+this.contracttype.contracttypeModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['service/contract-type-list']);      
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.contracttype[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.contractTypeForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.contracttypeId == 0){ 
				this.contracttype['requestType'] = 'add';
			}else{
				this.contracttype['requestType'] = 'update';
				this.contracttype['contracttypeId'] = this.contracttypeId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/contracttype',
									JSON.stringify(this.contracttype),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
	//			  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.contractTypeForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.contracttype);
	  }
 
  onDelete(){
		 this.contracttype['requestType'] = 'delete';
		this.contracttype['contracttypeId'] = this.contracttypeId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/contracttype',
									JSON.stringify(this.contracttype),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
//			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.contractTypeForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

export class  ContractTypeData {
    contracttypeId: string;
    contracttypeName: string;
	contracttypeEntryId: string;
	contracttypeEntryDate: string;
	contracttypeModifiedId: string;
	contracttypeModifiedDate: string;
  
}


